@import "../styles/app";

.auth-page {
  height: 100vh;
  background-color: #fff;
  padding: 0 40px;
  @include media-breakpoint-down(sm) {
    padding: 0;
  }

  img.backImg {
    position: absolute;
    right: 0;
    bottom: 0;
    max-height: 85%;
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }
}

.widget-auth {
  background: none !important;
  max-width: 360px;
  padding: 10px !important;
  height: 100vh;
  display: flex !important;
  flex-direction: column;
  justify-content: center;

  @include media-breakpoint-down(sm) {
    max-width: 100%;
  }

  input {
    border: 1px solid rgba(#323232, .2);
    border-radius: 8px;
    &::placeholder {
      color: rgba(#464646, .5)
    }
  }

  .form-check-label {
    font-size: 13px;
  }

  a {
    color: #FF7769;
    font-size: 13px;
  }

  .widget-auth-info {
    &.text-center {
      font-size: 13px;
    }
    font-size: 18px;
    color: #464646;
    margin-bottom: 20px;
  }

  .auth-btn {
    width: 100%;
  }

  .social-buttons {
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-direction: column;
    margin: ($spacer) 0;

    .social-button {
      display: flex;
      align-items: center;
      padding: 0;
      position: relative;
      height: 30px;
    }

    .social-icon {
      position: absolute;
      left: 1px;
      width: 26px;
      height: 26px;
      padding: 0;
      transition: all .2s ease-in-out;
      border-radius: 3px;
      background-color: $white;
    }

    .social-text {
      margin: 0 auto;
      font-size: $font-size-sm;
    }

    .social-google {
      background-size: 100%;
      background-image: url("data:image/svg+xml;base64,");
    }

    .social-microsoft {
      background-repeat: no-repeat;
      background-size: 50%;
      background-position-x: 50%;
      background-position-y: 50%;
    }

  }
}

.auth-footer {
  margin-bottom: 25px;
  font-size: 13px;
  color: #636c72;
  text-align: center;

  @media (min-height: 600px) {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

.auth-logo {
  margin-top: 15px;
  margin-bottom: 40px;
  text-align: center;
  font-weight: $font-weight-normal;

  i {
    font-size: 13px;
    margin: 0 20px;
  }
}


